.welcome {
    position: absolute;
    width: 30vw;
    height: 52vh;
    left: 35vw;
    bottom: 30vh;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

}

.header {
    color: #293264;
    font-size: 5vw;
    margin-bottom: 0.5vw;
    margin-top: 0.5vw;
   
}

.description {
    font-size: 1rem;
    margin-top: 0;
}

.form {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
}

/* Styling for the form container */

/* Styling for the labels */
.form label {
    display: block;
    margin-bottom: 5px;
    font-weight: bold;
    color: #333;
}

/* Styling for the select elements */
.form select {
    width: 100%;
    padding: 0.7rem;
    border-radius: 5px;
    border: 1px solid #ccc;
    margin-bottom: 15px;
    box-sizing: border-box;
}

/* Styling for the button */
.form button {
    background-color: #293264;
    color: white;
    padding: 0.7rem 1.3rem;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    text-align: center;
}

.form select:hover {
    box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
}

/* Styling for the button link */
.form button a {
    color: white;
    text-decoration: none;
}

/* Hover effect for the button */
.form button:hover {
    background-color: #1a203f;
    box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}