.question_form {
    margin-top: 7rem;
    
    
}

.question {
    border-bottom: 0.5px solid #DBDEF0;
    width: 80vw;
}

.question_text {
    font-size: 1.7rem;
    color: #293264;
    font-weight: 700;
}

.answers {
    display: flex;
    justify-content: space-evenly;
    margin-bottom: 1rem;
    
}

.choice {
    color: #293264;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.4px solid #293264;
    border-radius: 3rem;
    padding: 0.4rem 0.9rem 0.4rem 0.9rem;
    font-weight: 600;
}

.choice:hover {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
    cursor: pointer;
}

.choice label{
    cursor: pointer;
}

.radio {
    display: none;
}

.selected {
    border: none;
    background-color: #D6DBF5;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    border-radius: 3rem;
    padding: 0.4rem 0.9rem 0.4rem 0.9rem;
    color: #293264;
}

.selected:hover {
    box-shadow: rgba(0, 0, 0, 0.16) 0px 3px 6px, rgba(0, 0, 0, 0.23) 0px 3px 6px;
}

.submit {
    width: 12vw;
    height: 2.5rem;
    margin-left: 34vw;
    margin-top: 1rem;
    border: none;
    border-radius: 3rem;
    background-color: #4D5B9E;
    color: white;
    cursor: pointer;
    font-size: 1.25rem;
    display: flex;
    align-items: center;
    justify-content: center;
}

.submit:hover {
    box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 4px, rgba(0, 0, 0, 0.3) 0px 7px 13px -3px, rgba(0, 0, 0, 0.2) 0px -3px 0px inset;
}

/* after submit */

.correct{
    border: none;
    background-color: #94D7A2;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    border-radius: 3rem;
    padding: 0.4rem 0.9rem 0.4rem 0.9rem;
    color: #293264;
}

.unchosen {
    color: #293264;
    display: flex;
    justify-content: center;
    align-items: center;
    border: 0.4px solid #293264;
    border-radius: 3rem;
    padding: 0.4rem 0.9rem 0.4rem 0.9rem;
    font-weight: 600;
    opacity: 0.7;
}

.false {
    border: none;
    background-color: #F8BCBC;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: 600;
    border-radius: 3rem;
    padding: 0.4rem 0.9rem 0.4rem 0.9rem;
    color: #293264;
    opacity: 0.7;
}

.try_again { 
    color: white;
    font-size: 0.9rem;
    text-decoration: none;
}